* {
  box-sizing: border-box;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Muli", "Arial", sans-serif;
  color: #595666;
  font-weight: normal;
  font-style: normal;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: inline-block;
  vertical-align: top;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 5px;
  padding-bottom: 5px;
  font-family: "Muli", "Arial", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 16px;
  line-height: 30px;
  text-align: center;
  text-decoration: none;
  color: #2b2d5c;
  background-color: #f2c200;
  border: 0;
  border-top-right-radius: 12% 50%;
  border-bottom-right-radius: 12% 50%;
  border-top-left-radius: 12% 50%;
  border-bottom-left-radius: 12% 50%;
  cursor: pointer;
  z-index: 1;
}
.button:hover,
.button:focus {
  opacity: 0.7;
}
.button:active {
  opacity: 0.5;
}
@media (min-width: 798px) {
  .button {
    padding-left: 47px;
    padding-right: 66px;
    padding-top: 13px;
    padding-bottom: 17px;
    font-size: 16px;
    line-height: 30px;
  }
}
.container {
  max-width: 1600px;
  margin: 0 auto;
}
.header {
  padding-top: 30px;
  position: relative;
  background-color: #2b2d5c;
}
.header__wrapper {
  display: flex;
  flex-direction: column;
  padding-bottom: 20px;
}
.header__navigation {
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header__logo {
  margin-bottom: 10px;
  z-index: 1;
}
.header__logo span {
  display: inline-block;
  margin: 0;
  padding: 0;
  font-size: 20px;
  line-height: 32px;
  font-weight: 900;
  text-transform: uppercase;
  color: #ffffff;
}
.header__logo img {
  margin-right: 4px;
}
.header__nav {
  width: 100%;
}
.header__description {
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 1;
}
.header__description p:nth-of-type(1) {
  margin: 0;
  margin-bottom: 15px;
  padding: 0;
  text-align: center;
  font-family: "Trirong", serif;
  font-size: 30px;
  line-height: 22px;
  font-weight: 300;
  color: #ffffff;
}
.header__description p:nth-of-type(2) {
  margin: 0;
  margin-bottom: 50px;
  padding: 0;
  font-size: 10px;
  line-height: 22px;
  font-weight: 200;
  color: #ffffff;
  letter-spacing: 0.1px;
}
.header__button-down {
  margin: 0 auto;
  margin-bottom: 40px;
  position: relative;
  width: 50px;
  height: 37px;
}
.header__button-down a {
  font-size: 0;
}
.header__button-down a::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  top: 0;
  left: 11px;
  border-bottom: 1px solid #ddb30f;
  border-left: 1px solid #ddb30f;
  transform: rotate(-45deg);
}
.header__button-down a::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  top: 7px;
  left: 11px;
  border-bottom: 1px solid #ffffff;
  border-left: 1px solid #ffffff;
  transform: rotate(-45deg);
}
.header__button-down a:hover,
.header__button-down a:focus {
  opacity: 0.7;
}
.header__button-down a:active {
  opacity: 0.5;
}
.header__social {
  display: flex;
  justify-content: center;
}
.header__social ul {
  margin: 0;
  padding: 0;
  display: flex;
}
.header__social li {
  display: flex;
  align-items: flex-end;
  margin-right: 15px;
  list-style: none;
  z-index: 1;
}
.social__icon {
  fill: #ffffff;
}
.social__link:hover .social__icon,
.social__link:focus .social__icon {
  fill: #ffcc00;
}
.social__link:active .social__icon {
  opacity: 0.5;
}
